import React from 'react'

function PersonalInfo({formData, setData}) {
    return (
        <div className = "personal-info-container">
            <input type = 'text' placeholder = "Name"value = {formData.name}
                onChange = {event => setData({...formData, name: event.target.value})}/> 
            <input type = 'text' placeholder = "Address"value = {formData.address}
                onChange = {event => setData({...formData, address: event.target.value})}/>
            <input type = 'text' placeholder = "Phone"value = {formData.phone}
                onChange = {event => setData({...formData, phone: event.target.value})}/>
 
        </div>
    )
}

export default PersonalInfo
