import React from 'react'

function OtherInfo({formData, setData}) {
    return (
        <div className = "sign-up-container">
            <input type = 'text' placeholder = "About you"value = {formData.about} 
                onChange = {event => setData({...formData, about: event.target.value})}/>
        </div>
    )
}

export default OtherInfo
