import React from 'react'

function SingUpInfo({formData, setData}) {
    return (
        <div className = "sign-up-container">
            <input type = 'text' placeholder = "Email" value = {formData.email}
                onChange = {event => setData({...formData, email: event.target.value})}/>
            <input type = 'text' placeholder = "Password" value = {formData.password}
                onChange = {event => setData({...formData, password: event.target.value})}/>
            <input type = 'text' placeholder = "Confirm Password" value = {formData.confirlPassword}
                onChange = {event => setData({...formData, confirlPassword: event.target.value})}/> 
        </div>
    )
}

export default SingUpInfo
