import React, {useState} from 'react'
import OtherInfo from './OtherInfo'
import PersonalInfo from './PersonalInfo'
import SingUpInfo from './SingUpInfo'

function Form() {
    const [page, setPage] = useState(0)
    const FormTitle = ["Sing Up","Personal Info", "Other Info"]
    const [formData, setFormData] = useState({
        email: '',
        password : '',
        confirlPassword : '',
        name : '',
        address : '',
        phone : '',
        about : ''
    })
    const inputForm = ()=>{
        if(page === 0 )
            return <SingUpInfo formData = {formData} setData = {setFormData}/>
        else if(page === 1)
            return <PersonalInfo formData = {formData} setData = {setFormData}/>
        else
            return <OtherInfo formData = {formData} setData = {setFormData}/>
    }
    return (
        <div className = "form">
        <div className="progressbar">
            <div
            style={{ width: page === 0 ? "33.3%" : page === 1 ? "66.6%" : "100%" }}
            ></div>
        </div>
                <div className = "form-container">
                    <div className = "header" style= {{fontWeight: "bold"}}>{FormTitle[page]}</div>
                    <div className = "body">{inputForm()}</div>
                    <div className="footer">
                        <button disabled = {page === 0} onClick = {()=>{setPage(currentPage => currentPage - 1)}}>Prev</button>
                        <button
                        onClick = {()=>{
                            if(page === FormTitle.length - 1){
                                alert("From Submitted")
                                console.log(formData)
                            }
                            else{
                                setPage(currentPage => currentPage + 1)
                            }
                        }}>
                        {page === FormTitle.length - 1 ? "Submit" : "Next"}</button>
                    </div>
                </div>
        </div>
    )
}

export default Form
